import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import Vue from "vue";
import App from "./App";
import {
	router,
	RouterMount
} from "@/nxTemp/router";
import store from "@/nxTemp/store";
import uView from "@/uview-ui";
import nxTemp from "@/nxTemp";


async function bootstrap() {
	App.mpType = "app";
	//引入路由
	Vue.use(router);
	// 引入全局uView
	Vue.use(uView);
	// 加载nxTemp
	Vue.use(nxTemp);

	const app = new Vue({
		store,
		...App
	});




	app.$mount();

}

bootstrap();